
import { useForm, Controller } from "react-hook-form";
import { Dropdown } from "primereact/dropdown";
import { InputNumber } from 'primereact/inputnumber';
import { Button } from "primereact/button";
import { FieldError } from "../../../../../../../../shared/FieldError";

export const AddLimit = ({ i18n,vacationTypes,onSubmit, loading}) => {
  const {
    handleSubmit,
    control,
    formState: { errors },
  } = useForm({ mode: "onChange",defaultValues:{vacationType:"",daysLimit:null} });

  const { t } = i18n;							

  const loadingProps = {icon:"pi pi-spin pi-spinner",disabled:true}

  return (
    <form onSubmit={handleSubmit((fData)=>onSubmit(fData))}>
      <div className="form_fields_grid">

      <label className="align_self_center">{t("pages.vacations.createRequest.NewVacationRequestForm.fields.typeOfRequest.label")} * </label>
        <div className="form_input_container">
        <Controller name="vacationType" control={control} rules={{required:true}} render={({field})=><Dropdown placeholder={`${t("repeatedWords.choose")} ${t("pages.vacations.createRequest.NewVacationRequestForm.fields.typeOfRequest.label")}`} optionLabel={"name"} optionValue={"@id"} options={vacationTypes} value={field.value}  focusInputRef={field.ref} onChange={(e) => field.onChange(e.value)}/> }/>
        <FieldError fieldErr={errors.vacationType}/>
        </div>

        <label className="align_self_center">{t("repeatedWords.days")} *</label>
        <div className="form_input_container">
        <Controller name="daysLimit" control={control} rules={{required:true}} render={({field})=><InputNumber placeholder={`${t("repeatedWords.enter")} ${t("repeatedWords.days")}`} mode="decimal" showButtons min={1} max={1200} maxLength={4} value={field.value} onValueChange={(e) => field.onChange(e)} useGrouping={false}/>}/>
        <FieldError fieldErr={errors?.daysLimit}/>
        </div>

        <Button className="submit_button" label={t("repeatedWords.add")} {...(loading?loadingProps:{})}/>
      </div>
    </form>
  );
};
