import { useEffect, useMemo, useState } from "react";
import { NewVacationRequest as UI } from "./ui/NewVacationRequest";
import { getLabelForEmployees, getPlannedVacationDatesArr } from "./ui/lib";
import { api, getDateToSend, prepareEntityToSendById } from "../../../shared/api";
import { toastMessageConfig } from "../../../entities/toastMessageConfig";
import { removeEmptyKeys } from "../../../shared/api/lib/removeEmptyKeys";

export const NewVacationRequest = ({
  i18n,
  currentUser,
  className,
  type = "new",
  vacationTypes,
  employees,
  toastRef,
  departments,
  defaultValues
}) => {
  const [componentStatus, setComponentStatus] = useState({ status: "waiting" });
  const {t} = i18n

  const defaultValuesFormCopy= useMemo(()=>defaultValues?({...defaultValues}):undefined,[])
  if(defaultValuesFormCopy)getPlannedVacationDatesArr(defaultValuesFormCopy)

  const employeesFormCopy = useMemo(()=>([...employees]))
  useMemo(() => getLabelForEmployees(employeesFormCopy), []);


  const onSubmit = async (fData) => {
    setComponentStatus({ status: "loading" });

    const { plannedVacationDatesArr } = fData;

    fData.dateFrom = getDateToSend(new Date(plannedVacationDatesArr[0]));
    fData.dateTo = getDateToSend(new Date(plannedVacationDatesArr[1]));


    delete fData.plannedVacationDatesArr

    removeEmptyKeys(fData)
    
    const vacationActionResponse = await (type==="new"?api.vacations.post(fData):api.vacations.put(fData?.id,fData))

    const { warning, error } = vacationActionResponse;

    if (warning) {
      toastRef.current.show({
        severity: "warn",
        summary: warning.title,
        detail: warning.message,
        life: toastMessageConfig.life,
      });
      setComponentStatus({ status: "waiting" });
      return;
    }

    if (error) {
      toastRef.current.show({
        severity: "error",
        summary: error.title,
        detail: error.message,
        sticky: true,
      });
      setComponentStatus({ status: "waiting" });
      return;
    }

    toastRef.current.show({
      severity: "success",
      summary: t("repeatedWords.success"),
      detail: t("message.successfulOperation.message"),
      life: toastMessageConfig.life,
    });

    setComponentStatus({ status: "waiting" });
  };

  return (
      <UI
      i18n={i18n}
      className={className}
      type={type}
      vacationTypes={vacationTypes}
      employees={employeesFormCopy}
      onSubmit={onSubmit}
      loading={componentStatus.status === "loading"}
      departments={departments}
      defaultValues = {defaultValuesFormCopy}
      currentUser={currentUser}
    />
  );
};
