import "./style.css";
import { useForm,Controller } from "react-hook-form";
import { Dropdown } from "primereact/dropdown";
import { Calendar } from 'primereact/calendar';
import { InputText } from 'primereact/inputtext';
import { InputTextarea } from "primereact/inputtextarea";
import { Button } from "primereact/button";
import { getDaysBetweenDates, fieldsForWatchingArr,getDepartment,isFieldDisabled } from "./lib"
import { FieldError } from "../../../../shared/FieldError";
import { loadingProps } from "../../../../entities/loadingProps"
import { useEffect, useMemo } from "react";
import { getEmployeeById, getVacationTypeById } from "../../../../shared/lib";
import { getFieldsConfigByAccess } from "./lib/getFieldsConfigByAccess";

export const NewVacationRequest = ({ i18n,currentUser, className,type = "new", vacationTypes, employees, onSubmit, loading = false, departments, defaultValues }) => {
  
  const {
    watch,
    handleSubmit,
    formState: { errors },
    control,
    setValue
  } = useForm({ mode: "onChange", defaultValues });

  const { t } = i18n;			

  const [employeeIdValue,plannedVacationDatesArrValue] = watch(fieldsForWatchingArr);

  const department = useMemo(()=>getDepartment(employeeIdValue,employees),[employeeIdValue]) 
  const daysBetweenVacationDates = useMemo(()=>getDaysBetweenDates(plannedVacationDatesArrValue,true),[plannedVacationDatesArrValue]) 


  const typesOfRequestOptions = useMemo(()=>{

    if(defaultValues)return []

    const employee = getEmployeeById(employeeIdValue,employees)
    if(employee){
        if(employee.vacationLimits.length){
          const options = employee.vacationLimits.map(limit=>{
            const vacationType = getVacationTypeById(limit.vacationType,vacationTypes)
            return vacationType
          })
          return options
        }else return []
    }else return []
  },[employeeIdValue]) 

  const fieldsConfigByAccess = useMemo(()=>getFieldsConfigByAccess(currentUser,defaultValues),[])
  useEffect(()=>{if(fieldsConfigByAccess?.employee)setValue("employee",fieldsConfigByAccess.employee.value)},[])

  return (
    <form className={className?`NewVacationRequestForm ${className}`:"NewVacationRequestForm"} onSubmit={handleSubmit((fData)=>onSubmit(fData))}>
      <h3 className="NewVacationRequestForm_title">
        {type==="update"?t("pages.vacations.createRequest.NewVacationRequestForm.updateTitle"):t("pages.vacations.createRequest.NewVacationRequestForm.title")}
      </h3>

      <div className="form_fields_grid">

        <label className="align_self_center">{t("pages.vacations.createRequest.NewVacationRequestForm.fields.nameAndSurname.label")}</label>
        <div className="form_input_container">
        <Controller name="employee" control={control} rules={{required:true}} render={({field})=><Dropdown {...{disabled:fieldsConfigByAccess?.employee?.disabled}} {...isFieldDisabled(type)} placeholder={`${t("repeatedWords.choose")} ${t("pages.vacations.createRequest.NewVacationRequestForm.fields.replacement.placeholder")}`} optionValue={"@id"} options={employees} value={field?.value} onChange={(e) => field.onChange(e.value)} focusInputRef={field.ref}/> }/>
        <FieldError fieldErr={errors.employee}/>
        </div>

        <label>{t("pages.vacations.createRequest.NewVacationRequestForm.fields.positionDepartment.label")}</label>
        <p className="beUpColor">{department && department.name}</p>
        <label className="align_self_center">{t("pages.vacations.createRequest.NewVacationRequestForm.fields.typeOfRequest.label")}</label>
        <div className="form_input_container">
          {/* <Dropdown valueTemplate={typesOfRequestSelectOptionTemplate}  value={typeOfRequestSelectState} onChange={(e) => setTypeOfRequestSelectState(e.value)} options={groupedTypesOfRequestSelectOptions}  optionGroupLabel="label" optionGroupChildren="items"/> */}
        <Controller name="type" control={control} rules={{required:true}} render={({field})=><Dropdown {...isFieldDisabled(type)} placeholder={`${t("repeatedWords.choose")} ${t("pages.vacations.createRequest.NewVacationRequestForm.fields.typeOfRequest.label")}`} optionLabel={"name"} optionValue={"@id"} options={typesOfRequestOptions} value={field.value}  focusInputRef={field.ref} onChange={(e) => field.onChange(e.value)}/> }/>
        <FieldError fieldErr={errors.type}/>
        </div>

        <label className="align_self_center">{t("pages.vacations.createRequest.NewVacationRequestForm.fields.plannedVacation.label")}</label>
        <div className="form_input_container">
        <Controller name="plannedVacationDatesArr" control={control} rules={{required:true}} render={({field})=>
          <Calendar value={field.value} onChange={(e) => field.onChange(e.value)} selectionMode="range" readOnlyInput 
            showIcon showButtonBar 
            placeholder={t("pages.vacations.createRequest.NewVacationRequestForm.fields.plannedVacation.placeholder")}
            locale={i18n.resolvedLanguage}
          />}
        />
        <FieldError fieldErr={errors.plannedVacationDatesArr}/>
        </div>

        <label className="align_self_center">{t("pages.vacations.createRequest.NewVacationRequestForm.fields.vacationDaysNumber.label")}</label>
        <InputText value={daysBetweenVacationDates} className="NewVacationRequestForm_input_vacationDaysNumber beUpColor" disabled/>

        <label className="align_self_center">{t("pages.vacations.createRequest.NewVacationRequestForm.fields.replacement.label")}</label>
        <div className="form_input_container">
        <Controller name="replacement" control={control} render={({field})=><Dropdown placeholder={`${t("repeatedWords.choose")} ${t("pages.vacations.createRequest.NewVacationRequestForm.fields.replacement.placeholder")}`} optionValue={"@id"} options={employees} value={field.value} onChange={(e) => field.onChange(e.value)}/> }/>
        <FieldError fieldErr={errors.replacement}/>
        </div>

        <label>{t("pages.vacations.createRequest.NewVacationRequestForm.fields.comment.label")}</label>
        <Controller name="comment" control={control} render={({field})=><InputTextarea {...isFieldDisabled(type)} {...field} autoResize rows={5} cols={30} maxLength={200} placeholder={t("pages.vacations.createRequest.NewVacationRequestForm.fields.comment.placeholder")}/> }/>
       
        <Button className="submit_button" label={type==="update"?t("repeatedWords.update"):t("pages.vacations.createRequest.NewVacationRequestForm.submitButton")} {...(loading?loadingProps:{})}/>
      </div>
    </form>
  );
};
